function getValuesByKey(array, key) {
    return array.map(data => data[key]);
}

function unique(array) {
    return [...new Set(array)]
}

function sum(array) {
    return array.reduce((accValue, value) => (accValue + value));
}

function flattenOnce(array) {
    return array.reduce((acc, el) => [...acc, ...el]);
}

function product2(x, y) {
    const product = x.map(elx => y.map(ely => {
        if (Array.isArray(elx)) {
            return [...elx, ely];
        } else {
            return [elx, ely];
        }
    }));
    return flattenOnce(product);
}

function product(arrs) {
    if (arrs.length === 1) {
        return arrs;
    }

    return arrs.reduce((accArray, array) => product2(accArray, array));
}

function difference(x, y) {
    return x.filter(e => !y.includes(e));
}

function judgeFoods(itemData) {
    if (itemData.length === 0) {
        return [];
    }

    const fairyOrDubious = () => {
        return itemCategories.includes("妖精") ? ["妖精の力水"] : ["微妙な料理"];
    }

    const itemCategories = getValuesByKey(itemData, "category");

    if (itemCategories.includes("鉱石")) {
        return itemCategories.includes("妖精") ? ["妖精の力水"] : ["硬すぎ料理"];
    }

    const effect = judgeEffect(itemData, "");
    const prefix = getPrefix(effect);
    if (itemCategories.includes("生物") && itemCategories.includes("魔物")) {
        if (effect === "効果無し") {
            return fairyOrDubious();
        }
        return [`${prefix}薬`];
    }

    if (!itemCategories.includes("生物") && !itemCategories.includes("魔物")) {
        const foods = getFoods(itemData);
        if (foods.length === 0) {
            return fairyOrDubious();
        }
        return foods.map(food => `${prefix}${food}`);
    }

    return ["微妙な料理"];
}

function judgeEffect(itemData, foods) {
    if (itemData.length === 0 || ["硬すぎ料理", "微妙な料理", "妖精の力水"].includes(foods[0])) {
        return "効果無し";
    }

    const itemEffects = getValuesByKey(itemData, "effect");

    const uniqEffects = unique(itemEffects);
    const uniqEffectsNoBrank = uniqEffects.filter(effect => {
        return effect !== "効果無し";
    });

    if (uniqEffectsNoBrank.length === 1) {
        return uniqEffectsNoBrank[0];
    }
    return "効果無し";
}

function getPrefix(effect) {
    const prefixOf = {
        "MAXハート": "マックス",
        "がんばり回復": "がんばり",
        "MAXがんばり": "スタミナ",
        "移動力アップ": "ゴーゴー",
        "攻撃アップ": "チカラ",
        "防御アップ": "カチコチ",
        "静かさアップ": "しのび",
        "寒さガード": "ピリ辛",
        "暑さガード": "ひんやり",
        "エレキガード": "エレキ",
        "炎ガード": "燃えず",
        "効果無し": ""
    }

    return prefixOf[effect];
}

import generalNames from "../assets/generalName.json";
import sortedRecipes from "../assets/sortedRecipes.json";

function getFoods(itemData) {
    const items = getValuesByKey(itemData, "name");

    //各素材が属する、最も大きなカテゴリー（総称）の配列
    const itemLargeCategories = items.map(item => {
        if (item in generalNames) {
            return generalNames[item][0];
        } else {
            return item;
        }
    });

    const uniqItemLargeCategories = unique(itemLargeCategories);
    const copiousFoodsOf = {
        "[果実]": ["山盛煮込み果実"],
        "[キノコ]": ["山盛焼きキノコ"],
        "[野草]": ["山盛焼き山菜"],
        "[肉]": ["山盛串焼き肉"],
        "[魚]": ["山盛串焼き魚"]
    }
    if (items.length >= 4 && uniqItemLargeCategories.length == 1 && unique(items).length >= 4) {
        return copiousFoodsOf[uniqItemLargeCategories[0]];
    }

    //「各素材ごとにそれが属するカテゴリー（総称）の配列」の配列
    const itemsCategories = items.map(item => {
        if (item in generalNames) {
            return [item, ...generalNames[item]];
        } else {
            return [item];
        }
    });

    //カテゴリーの、あり得る全ての組み合わせ
    const categoriesProducts = product(itemsCategories);

    const foods = [];
    for (let i = 5; i >= 1; i--) {
        Object.keys(sortedRecipes[i]).forEach(food => {
            categoriesProducts.forEach(product => {
                if (difference(sortedRecipes[i][food], product).length === 0) {
                    const lastChar = food.slice(-1);
                    if (lastChar > -1) {
                        food = food.slice(0, -1);
                    }
                    foods.push(food);
                }
            });
        });

        if (foods.length !== 0) {
            return unique(foods);
        }
    }
    if (foods.length === 0) {
        return foods;
    }
    throw new Error("[Midreeca] something went wrong...")
}

function calcHeart(itemData, foods, effect) {
    if (itemData.length === 0 || effect == "MAXハート") {
        return "-";
    }

    const uniqItems = unique(getValuesByKey(itemData, "name"));
    const nutCountOf = { "どんぐり": 0, "小鳥の木の実": 0 };

    let heart = 0;
    switch (foods[0]) {
        case "硬すぎ料理":
            return 0.25;

        case "微妙な料理":
            itemData.forEach(item => {
                if (["ドラゴン", "マモノエキス"].includes(item["category"])) {
                    heart += 0;
                } else {
                    heart += item["heart"];
                }
            });
            heart /= 2;
            if (heart < 1) {
                heart = 1;
            }
            return heart;

        case "妖精の力水":
            itemData.forEach(item => {
                if (item["category"] === "ナッツ") {
                    if (uniqItems.length == 1) {
                        heart += 0.5;
                    } else {
                        if (nutCountOf[item["name"]] === 0) {
                            heart += 1;
                        } else {
                            heart += 0.5;
                        }
                    }
                    nutCountOf[item["name"]]++;
                } else if (item["category"] === "妖精") {
                    heart += 7;
                } else {
                    heart += item["heart"];
                }
            });
            return heart;

        default:
            itemData.forEach(item => {
                if (item["category"] === "ナッツ") {
                    if (uniqItems.length == 1) {
                        heart += 0.5;
                    } else {
                        if (nutCountOf[item["name"]] === 0) {
                            heart += 1;
                        } else {
                            heart += 0.5;
                        }
                    }
                    nutCountOf[item["name"]]++;
                } else {
                    heart += item["heart"];
                }
            });
            return heart;
    }
}


function calcPrice(itemData, foods) {
    if (itemData.length === 0) {
        return "-";
    }

    if (["硬すぎ料理", "微妙な料理", "妖精の力水"].includes(foods[0])) {
        return 2;
    }

    const prices = getValuesByKey(itemData, "price");
    let price = sum(prices);

    const weightRateOf = { 1: 1.5, 2: 1.8, 3: 2.1, 4: 2.4, 5: 2.8 };
    price *= weightRateOf[itemData.length];
    price = Math.floor(price);
    price = 10 * Math.ceil(0.1 * price);

    return price;
}

function calcLevel(itemData, effect) {
    const effectPoints = getValuesByKey(itemData, "effectPoint");
    let effectPoint = sum(effectPoints);

    switch (effect) {
        case "MAXハート":
            return `ハート${effectPoint}個追加`;

        case "MAXがんばり":
            effectPoint = Math.floor(effectPoint);
            effectPoint /= 5;
            if (effectPoint < 0.2) {
                effectPoint = 0.2;
            }
            return `ゲージ${effectPoint}周追加`;

        case "がんばり回復":
            effectPoint = Math.floor(effectPoint);
            effectPoint /= 5;
            if (effectPoint < 0.2) {
                effectPoint = 0.2;
            }
            return `ゲージ${effectPoint}周`;

        case "暑さガード":
        case "寒さガード":
            if (effectPoint >= 6) {
                return "Lv.2";
            } else {
                return "Lv.1";
            }

        case "エレキガード":
            if (effectPoint >= 6) {
                return "Lv.3";
            } else if (effectPoint >= 4) {
                return "Lv.2";
            } else {
                return "Lv.1";
            }

        case "炎ガード":
            if (effectPoint >= 7) {
                return "Lv.2";
            } else {
                return "Lv.1";
            }

        case "攻撃アップ":
        case "防御アップ":
        case "移動力アップ":
            if (effectPoint >= 7) {
                return "Lv.3";
            } else if (effectPoint >= 5) {
                return "Lv.2";
            } else {
                return "Lv.1";
            }

        case "静かさアップ":
            if (effectPoint >= 9) {
                return "Lv.3";
            } else if (effectPoint >= 6) {
                return "Lv.2";
            } else {
                return "Lv.1";
            }

        default:
            throw new Error("[Midreeca] irregal effect name.");
    }
}


function calcDuration(itemData, effect) {
    if (["MAXハート", "MAXがんばり", "がんばり回復"].includes(effect)) {
        return "-";
    }

    let mins = 0;
    let secs = 0;
    let min, sec;

    const durations = getValuesByKey(itemData, "duration");
    durations.forEach(duration => {
        if (duration !== "") {
            min = duration.split(":")[0];
            sec = duration.split(":")[1];
            mins += parseInt(min);
            secs += parseInt(sec);
        }
    });

    const secsToMins = Math.floor(secs / 60);
    mins += secsToMins;
    secs -= secsToMins * 60;

    if (mins > 30) {
        mins = 30;
        secs = 0;
    }

    return `${mins}:${("0" + secs).slice(-2)}`
}

export default {
    getValuesByKey: getValuesByKey,
    judgeFoods: judgeFoods,
    calcPrice: calcPrice,
    judgeEffect: judgeEffect,
    calcLevel: calcLevel,
    calcDuration: calcDuration,
    calcHeart: calcHeart
}