<template>
  <div id="filter-display">
    <button
    type="button"
    id="tab-effect"
    :class="['tab', {'tab-active': currentTab === 'effect'}]"
    @click="currentTab = 'effect'">
      効果で絞る
    </button>
    <button
    type="button"
    id="tab-category"
    :class="['tab', {'tab-active': currentTab === 'category'}]"
    @click="currentTab = 'category'">
      種類で絞る
    </button>
    <FilterTargetSelecter
    v-show="currentTab === 'effect'"
      id="effect-filter"
      :filterTargets="effects"
      @updateSelectedTargets="selectedEffects = $event; emitSelectedTargetsLists()"
    />
    <FilterTargetSelecter
    v-show="currentTab === 'category'"
      id="category-filter"
      :filterTargets="categories"
      @updateSelectedTargets="selectedCategories = $event; emitSelectedTargetsLists()"
    />
  </div>
</template>

<script>
import FilterTargetSelecter from "./FilterTargetSelecter.vue"

export default {
  components: {
    FilterTargetSelecter
  },
  data: function() {
    return {
      currentTab: "effect",
      effects: [
        { name: "MAXハート", id: 1 },
        { name: "がんばり回復", id: 2 },
        { name: "MAXがんばり", id: 3 },
        { name: "移動力アップ", id: 4 },
        { name: "攻撃アップ", id: 5 },
        { name: "防御アップ", id: 6 },
        { name: "静かさアップ", id: 7 },
        { name: "暑さガード", id: 8 },
        { name: "寒さガード", id: 9 },
        { name: "エレキガード", id: 10 },
        { name: "炎ガード", id: 11 },
        { name: "効果無し", id: 0 }
      ],
      selectedEffects: [],
      categories: [
        { name: "果実", id: 1 },
        { name: "キノコ", id: 2 },
        { name: "野草", id: 3 },
        { name: "肉", id: 4 },
        { name: "雑多", id: 5 },
        { name: "ドラゴン", id: 6 },
        { name: "魚介", id: 7 },
        { name: "生物", id: 8 },
        { name: "鉱石", id: 9 },
        { name: "魔物", id: 10 }
      ],
      selectedCategories: []
    };
  },
  methods: {
    emitSelectedTargetsLists: function() {
      this.$emit("updateSelectedTargetsLists",
        [this.selectedEffects, this.selectedCategories]
      );
    }
  }
};
</script>

<style scoped>
#filter-display {
  padding: 0.3rem;
}

.tab {
font-size: 0.8rem;
padding: 0.2rem 0.4rem;
border-top-left-radius: 0.4rem;
border-top-right-radius: 0.4rem;
border: none;
cursor: pointer;
background: rgba(244, 252, 182, 0.452);
margin-right: 0.1rem;
}

.tab-active {
  background-color: rgb(244, 252, 182);

}


</style>