<template>
  <div>
    <p style="display:none">{{selectedTargets}}</p>
      <!-- これ↑を入れたほうが、無表示バグが置きにくい気がする -->
    <transition-group
      name="img-items"
      tag="div"
      id="img-item-container"
      v-show="isImg"
    >
      <item-button
        class="item-button"
        v-for="item in items"
        :key="item.id"
        :itemName="item.name"
        :imgSrc="item.path"
        @click.native="clickItem(item)"
      />
    </transition-group>
    <table
      id="info-item-container"
      v-show="!isImg"
    >
      <thead>
          <th>名前</th>
          <th>種類</th>
          <th>食材回復量</th>
          <th>効果</th>
          <th>効果ポイント</th>
          <th>効果時間</th>
          <th>料理売値</th>

      </thead>
      <transition-group
        name="info-items"
        tag="tbody"
      >
        <tr
          class="item-row"
          v-for="item in items"
          :key="item.id"
          @click="clickItem(item)"
        >
          <td>{{item.name}}</td>
          <td>{{item.category}}</td>
          <td>{{item.heart}}</td>
          <td>{{item.effect}}</td>
          <td>{{item.effectPoint}}</td>
          <td>{{item.duration}}</td>
          <td>{{item.price}}</td>

        </tr>
      </transition-group>
    </table>
  </div>
</template>

<script>
import ItemButton from "./ItemButton.vue";
import itemData from "../assets/itemData.json";

export default {
  props: {
    isImg: {
      type: Boolean,
      required: true
    },
    selectedTargets: {
      type: Array,
      required: true
    }
  },
  components: {
    ItemButton
  },
  data: function() {
    return {
      itemData: itemData,
      selectedItemsWithKey: [],
      nextKey: 0
    };
  },
  computed: {
    items: function() {
      const selectedEffects = this.selectedTargets[0];
      const selectedCategories = this.selectedTargets[1];
      return this.itemData.filter(item => {
        const isOkOnEffect =  selectedEffects.includes(item.effect);
        const isOkOnCategories = 
          selectedCategories.includes(item.category)
            ||
            ( selectedCategories.includes("雑多")
              &&
              ["ナッツ", "マモノエキス", "妖精"].includes(item.category)
            )
        return isOkOnEffect && isOkOnCategories;
      });
    }
  },
  methods: {
    clickItem: function(item) {
      if (this.selectedItemsWithKey.length >= 5) {
        console.error("[Midreeca] too many selected items.");
        return;
      }

      this.selectedItemsWithKey.push({ item: item, key: this.key() });
      this.$emit("clickItem", this.selectedItemsWithKey);
    },
    key: function() {
      return this.nextKey++;
    }
  }
};
</script>

<style scoped>
#img-item-container {
  margin: 0.3rem;

  display: grid;
  grid-template-columns: repeat(auto-fit, min(20%, 6rem));
  justify-content: center;
}

#info-item-container {
  margin: 0.3rem;    
  text-align: center;
  width: calc(100% - 0.3rem * 2);
}


th, td {
  padding: 0.2rem 0;
  vertical-align: middle;
}

thead {
  background-color: rgb(104, 165, 255);
}

.item-row {
  background-color: rgb(250, 254, 255);
}

.item-row:nth-child(odd) {
  background-color: rgb(223, 248, 255);
}

.item-row:hover {
  background-color: rgb(161, 213, 255);
}

/* transition */
.item-button, .item-row {
  transition: all 0.5s;
}

.item-row {
  transition: background-color 0.1s;
}

.img-items-enter,
.img-items-leave-to,
.info-items-enter,
.info-items-leave-to {
  opacity: 0;
}

.img-items-enter-active,
.img-items-leave-active,
.info-items-enter-active,
.info-items-leave-active {
  transition: all 0s;
}

.img-items-leave-active,
.info-items-leave-active {
  position: absolute;
  visibility: hidden;
}

.img-items-move,
.info-items-move {
  transition: all 0.5s;
}
</style>