<template>
  <div class="filter">
    <button type="button" id="check-all-button" @click="checkAll" :disabled="isAllChecked">全選択</button>
    <button type="button" id="uncheck-all-button" @click="uncheckAll" :disabled="isAllUnchecked">全解除</button>
    <div class="target-container">
      <span v-for="target in filterTargets" :key="target.id">
        <input
          type="checkbox"
          :id="target.name"
          :name="target.name"
          :value="target.name"
          v-model="selectedTargets"
          @change="emitSelectedTargets"
        />
        <!-- v-model内の @change="{selecetdTargetsの更新}" と、
                        上記 @change="emitSelectedTargets" の実行順序は信頼無し？
        だけどうまくこの順で動いてるっぽい-->
        <label class="effect-button" :for="target.name">{{target.name}}</label>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    filterTargets: {
      type: Array,
      required: true,
      validator: function(tragets) {
        return tragets.every(target => "name" in target && "id" in target);
      }
    }
  },
  data: function() {
    return {
      selectedTargets: []
    };
  },
  created: function() {
    this.checkAll();
  },
  computed: {
    isAllChecked: function() {
      return this.selectedTargets.length === this.filterTargets.length;
    },
    isAllUnchecked: function() {
      return this.selectedTargets.length === 0;
    }
  },
  methods: {
    checkAll: function() {
      this.selectedTargets = this.filterTargets.map(target => target.name);
      this.emitSelectedTargets();
    },
    uncheckAll: function() {
      this.selectedTargets = [];
      this.emitSelectedTargets();
    },
    emitSelectedTargets: function() {
      this.$emit("updateSelectedTargets", this.selectedTargets);
    }
  }
};
</script>

<style scoped>
.filter{
  background-color: rgb(244, 252, 182);
}

#check-all-button,
#uncheck-all-button {
  background-color: rgb(253, 255, 235);
  margin: 0.3rem;
  font-size: 0.7rem;
  cursor: pointer;
}


#check-all-button:disabled,
#uncheck-all-button:disabled {
  background-color: rgb(255, 255, 255);
  color: #ccc;
  cursor: default;
}

.target-container {
  margin: 0;
}

input {
  display: none;
}

.effect-button {
  display: inline-block;
  width: calc(100% - 0.2rem);
  font-size: 0.9rem;
  text-align: center;
  padding: 0.2rem 0;
  margin: 0.1rem;
  cursor: pointer;
  color: #999999;
  background-color: #eee;
}

input:checked + .effect-button {
  color: rgb(7, 36, 26);
  background-color: rgb(178, 245, 90);
}

.effect-button:hover {
  background-color: rgb(221, 235, 204);
}

input:checked + .effect-button:hover {
  background-color: rgb(210, 243, 167);
}

@media screen and (min-width: 960px) {
  .target-container {
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6));
  }
}

@media screen and (max-width: 959px) {
  .target-container {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
  }
}
</style>