<template>
  <div>
    <p id="selected-items-label">選択された素材:</p>
    <transition-group name="selected-items" tag="div" class="selected-items">
      <ItemButton
        class="selected-item-button"
        v-for="(itemData, index) in selectedItemsWithKey"
        :key="itemData.key"
        :itemName="itemData.item.name"
        :imgSrc="itemData.item.path"
        :isMin="true"
        @click.native="selectedItemsWithKey.splice(index, 1)"
      />
      <div
        id="no-item-placeholder"
        key="-1"
        v-if="selectedItemsWithKey.length === 0"
      >
        <ItemButton id="dummy-item" itemName="d" imgSrc="./img/apple.png" :isMin="true"/>
        <p id="placeholder-text">なし</p>
      </div>
    </transition-group>
  </div>
</template>

<script>
import ItemButton from "./ItemButton.vue";

export default {
  components: {
    ItemButton
  },
  props: {
    selectedItemsWithKey: {
      type: Array,
      required: true,
      validator: function(items) {
        return items.length <= 5;
      }
    }
  }
};
</script>

<style scoped>
#selected-items-label {
  padding: 0.5rem 0.8rem 0.3rem;
}

#no-item-placeholder{
  grid-column: 3;
  /*position: relative; /*これを付けるとleave時にcol3線につっかかる*/
  display: flex;
  justify-content: center;
  align-items: center;
}

#dummy-item{
  visibility: hidden;
}

#placeholder-text{
  position: absolute;
  text-align: center;
}

.selected-items {
  display: grid;
  grid-template-columns: repeat(5, 20%);
}

/* transition */
.selected-item-button {
  transition: all 0.5s;
  display: inline-block;
}

.selected-items-enter {
  opacity: 0;
  transform: translateX(100%);
}

.selected-items-leave-to {
  opacity: 0;
  transform: translateX(-100%);
}

.selected-items-enter-active,
.selected-items-leave-active {
  transition: all 0.5s;
}

.selected-items-leave-active {
  position: absolute;
  width: calc(20% - 5px * 2);
}

.selected-items-move {
  transition: all 0.5s;
}
</style>