<template>
  <div>
    <div id="result-switcher-container">
      <button
        type="button"
        id="result-switcher"
        @click="isTableHidden = !isTableHidden"
      >{{isTableHidden ? "△ 全表示" : "▽ 一部非表示"}}</button>
    </div>
    <SelectedItems id="selected-items" :selectedItemsWithKey="selectedItemsWithKey" />
    <table id="item-data-table" :class="{'hide-on-portrait': isTableHidden}">
      <tr>
        <th>料理候補</th>
        <td>
          <div id="foods-cell">
            <p class="food" v-for="(food, index) in foods" :key="index">{{food}}</p>
          </div>
        </td>
      </tr>
      <tr>
        <th>回復量</th>
        <td>{{heart}}</td>
      </tr>
      <tr>
        <th>売値</th>
        <td>{{price}}</td>
      </tr>
      <tr>
        <th>効果種類</th>
        <td>{{effectName}}</td>
      </tr>
      <tr>
        <th>効果レベル</th>
        <td>{{effectLevel}}</td>
      </tr>
      <tr>
        <th>効果持続時間</th>
        <td>{{effectDuration}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import SelectedItems from "./SelectedItems.vue";
import c from "./resultCalculator.js";

export default {
  components: {
    SelectedItems
  },
  props: {
    selectedItemsWithKey: {
      type: Array,
      required: true
    }
  },
  data: function() {
    return {
      isTableHidden: false
    };
  },
  computed: {
    selectedItems: function() {
      return this.selectedItemsWithKey.map(itemWithKey => itemWithKey["item"]);
    },
    hasMonsterExtract: function() {
      const itemNames = c.getValuesByKey(this.selectedItems, "name");
      return itemNames.includes("マモノエキス");
    },

    foods: function() {
      return c.judgeFoods(this.selectedItems);
    },
    price: function() {
      return c.calcPrice(this.selectedItems, this.foods);
    },
    effectName: function() {
      return c.judgeEffect(this.selectedItems, this.foods);
    },
    effectLevel: function() {
      if (this.effectName === "効果無し") {
        return "-";
      }
      let level = c.calcLevel(this.selectedItems, this.effectName);
      if (this.hasMonsterExtract) {
        if (level === "Lv.1") {
          level = "Lv.1 or Lv.2";
        } else if (level === "Lv.2") {
          level = "Lv.1 or Lv.2 or Lv.3";
        } else if (level === "Lv.3") {
          level = "Lv.2 or Lv.3";
        }
      }
      return level;
    },
    effectDuration: function() {
      if (this.effectName === "効果無し") {
        return "-";
      }
      let duration = c.calcDuration(this.selectedItems, this.effectName);
      if (this.hasMonsterExtract && duration !== "-") {
        duration = "1:00 or 10:00 or 30:00";
      }
      return duration;
    },
    heart: function() {
      let heart = c.calcHeart(this.selectedItems, this.foods, this.effectName);

      if (this.hasMonsterExtract && heart != "-") {
        heart = `${heart + 3} or ${heart} or 0.25`;
      }
      return heart;
    }
  }
};
</script>

<style scoped>
#result-switcher-container {
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 560px) {
  #result-switcher-container {
    display: none;
  }
}

#result-switcher {
  background-color: #fff;
}

#selected-items{
  background-color: rgb(246, 254, 255);
  margin: 0.3rem;
}

#item-data-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 2px;
  padding: 2px;
  padding-bottom: 0.5vh;
}

th,
td {
  background-color: rgb(240, 253, 255);
  vertical-align: middle;
  padding: 0.5rem;
}

.food {
  margin: 0.5rem 0;
}

.food:first-child {
  margin-top: 0;
}

.food:last-child {
  margin-bottom: 0;
}

@media screen and (max-width: 559px) {
  .hide-on-portrait {
    display: none;
  }
}
</style>