<template>
  <div id="app">
    <header id="main-header">
      <span>BotW料理自動判定</span>
    </header>
    <div id="filter-container">
      <ItemFilter
        @updateSelectedTargetsLists="selectedTargets=$event"
      />
    </div>
    <button
      type="button"
      id="table-switcher"
      @click="isImg = !isImg"
    >
      <img src="./assets/grid.png" alt="grid" v-show="isImg" class="switcher-img">
      <img src="./assets/row.png" alt="row" v-show="!isImg" class="switcher-img">
    </button>
    <div id="table-container">
      <ItemTable
        id="item-table"
        :isImg="isImg"
        :selectedTargets="selectedTargets"
        @clickItem="selectedItemsWithKey=$event"
      />
    </div>
    <div id="result-container">
      <ResultDisplay id="result-display" :selectedItemsWithKey="selectedItemsWithKey" />
    </div>
  </div>
</template>

<script>
import ItemFilter from "./components/ItemFilter.vue";
import ItemTable from "./components/ItemTable.vue";
import ResultDisplay from "./components/ResultDisplay.vue";

export default {
  name: "App",
  components: {
    ItemFilter,
    ItemTable,
    ResultDisplay
  },
  data: function() {
    return {
      isImg: true,
      selectedItemsWithKey: [],
      selectedTargets: [],
    };
  }
};
</script>

<style>
#app {
  overflow-x: hidden;

  height: 100vh;
  display: grid;
  position: relative;
}
#main-header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(91, 202, 119);
  font-size: 1.5rem;
  text-align: center;

  grid-area: header;
}

#filter-container {
  grid-area: filter;
  background-color: rgb(219, 175, 80);
}

#table-switcher{
  grid-area: item;
  position: absolute;
  bottom: 1rem;
  right: 1rem;


  width: 3rem;
  height: 3rem;
  padding: 0.5rem;

  background-color: rgb(255, 202, 122);
  box-shadow: 0 0 0.2rem 0.2rem rgba(61, 47, 20, 0.466);
  opacity: 0.8;
  transition: all 0.1s;

  border: none;
  border-radius: 2rem;

}

#table-switcher:hover {
  opacity: 1;
}

#table-switcher:active {
  opacity: 1;
  box-shadow: 0 0 0.4rem 0.4rem rgba(61, 47, 20, 0.466);
}

.switcher-img {
  width: 100%;
  height: 100%;
  padding: 0.1rem;
}

#table-container {
  grid-area: item;
  overflow-y: scroll;
}

#result-container {
  background-color: rgb(215, 244, 255);
  overflow-x: hidden;

  grid-area: result;
  overflow-y: scroll;

  position: relative; /* よくわからんが、これがないと、
    selected-itemがleaveするときにwidthが20vwになってしまう */
}

@media screen and (min-width: 560px) {
  #app {
    grid-template-rows: 3rem max-content 1fr;
    grid-template-areas:
      "header header"
      "filter result"
      "item   result";
  }
}

@media screen and (min-width: 768px) {
  #app {
    grid-template-columns: 1fr 368px;

  }
}

@media screen and (min-width: 560px) and (max-width: 767px) {
  #app {
    grid-template-columns: 50% 50%;
  }
}

@media screen and (max-width: 559px) {
  #app {
    grid-template-rows: 3rem max-content 1fr max-content;
    grid-template-areas:
      "header"
      "filter"
      "item  "
      "result";
  }
}
</style>
